<div><v-layout><v-app-bar id="color" absolute style="overflow: visible"><v-app-bar-nav-icon @click="clickdrawer()"></v-app-bar-nav-icon><v-app-bar-title><p>Mes compétences</p></v-app-bar-title></v-app-bar><v-navigation-drawer class="overflow-" id="navigationdrawer" v-model="drawer" absolute temporary><v-list nav dense><div v-for="item in redirectitems"><v-list-item :prepend-icon="item.icon" :title="item.title" @click="redirectnavbar(item.path)"></v-list-item></div></v-list></v-navigation-drawer><v-main><v-card class="mx-auto overflow-hidden" height="100vh" width="0vw"></v-card></v-main><div id="main"><v-card id="ecartcategorie" elevation="0"></v-card><v-card id="ecartcategorie" elevation="0"></v-card><div style="width: 90vw"><a id="white" href="../assets/CV-Romain-MURIER.pdf" download="CV-Romain-MURIER.pdf">Télécharger mon CV</a><br><br><br><v-card style="width: 70vw"><v-card-title><a>Mon projet professionnel</a></v-card-title><v-card-text><p>Devenir développeur logiciel, bien sur !</p><br><p>Et partir d'entrée de jeu en alternance jusqu'à bac+2 / 3, pour commencer.</p><p>Mobile France entière: Ce sont l'environnement professionnel et les missions qui m'intéressent :</p><p>Parce qu'il n'y a rien de mieux que l'entreprise pour appliquer les apprentissages de l'école, pratiquer, s'immerger dans le monde professionnel,<a> travailler en équipe, échanger des points de vue, et surtout s'impliquer dans des projets concrets et utiles.</a></p><p>Et voir du pays, c'est un plus ! ;-)</p><br><p>Continuer jusqu'au master, sans doute, dans une orientation client applicatif, probablement en .NET.</p><br><p>Mais chaque chose en son temps ^^ !</p></v-card-text></v-card><v-card id="ecartcategorie" elevation="0"></v-card><v-card style="width: 70vw"><v-card-title><p>Mes Softskills</p></v-card-title><v-card-text><lu><li> <b>Curieux : </b><a>Si c'est utile à mon projet ou ma mission, ça m'intéresse !!</a></li><li> <b>Autonome :</b><a> Pour mes formations, je me suis renseigné, j'ai suivi des tutoriels, trouvé des projets pour appliquer.</a></li><li> <b>Esprit d'équipe : </b><a>L'alternance c'est aussi sortir du cadre scolaire et intégrer une équipe professionnelle, et nouer des relations de travail pour travailler en synergie. </a></li><li> <b>Tenace : </b><a>En difficulté, je cherche, je m'accroche et je me dépasse.</a></li><li> <b>Flexible : </b><a>Je m'adapte aux situations et environnements, j'aime quand ca bouge (et quoi de mieux que l'informatique pour ça) !</a></li></lu></v-card-text></v-card><v-card id="ecartcategorie" elevation="0"></v-card><v-card style="width: 70vw"><v-card-title><a>Mes connaissances en langues</a></v-card-title><v-card-text><table><tr><th width="1%"> </th><th width="1%"> </th></tr><tr><td id="center">Anglais</td><td> <img src="https://zupimages.net/up/22/16/yj9j.png" alt="3" width="100"></td></tr><tr><td id="center">Espagnol</td><td><img src="https://zupimages.net/up/22/16/uetb.png" alt="1.5" width="100"></td></tr></table></v-card-text></v-card><v-card id="ecartcategorie" elevation="0"></v-card><v-card style="width: 70vw"><v-card-title><p>Mes connaissances informatiques actuelles</p></v-card-title><v-card-text><p>J'ai à peine 18 ans, je suis loin d'avoir 10 ans d'expérience, mais je ne pars pas de rien (voir <a id="link" @click="redirectnavbar('/projects')">Mes réalisations</a><a>) ^^ !</a></p></v-card-text><v-card elevation="0"><v-card-title><p>Langages</p></v-card-title><v-card-text><table><tr><th width="1%"> </th><th width="1%"> </th></tr><tr><td id="center">Python</td><td> <img src="https://zupimages.net/up/22/16/yj9j.png" alt="3" width="100"></td></tr><tr><td id="center">HTML / CSS</td><td><img src="https://zupimages.net/up/22/16/uetb.png" alt="1.5" width="100"></td></tr><tr><td id="center">JavaScript</td><td><img src="https://zupimages.net/up/22/16/mfp5.png" alt="2" width="100"></td></tr><tr><td id="center">SQL</td><td><img src="https://zupimages.net/up/22/16/9dl1.png" alt="2.5" width="100"></td></tr><tr><td id="center">C#</td><td><img src="https://zupimages.net/up/22/16/nubq.png" alt="1" width="100"></td></tr></table></v-card-text></v-card><v-card elevation="0"><v-card-title><p>Environnements</p></v-card-title><v-card-text><table><tr><th width="1%"> </th><th width="1%"> </th></tr><tr><td id="center">Visual Studio Code</td><td> <img src="https://zupimages.net/up/22/16/yj9j.png" alt="3" width="100"></td></tr><tr><td id="center">Visual Studio</td><td><img src="https://zupimages.net/up/22/16/mfp5.png" alt="2" width="100"></td></tr><tr><td id="center">PGAmin 4</td><td><img src="https://zupimages.net/up/22/16/9dl1.png" alt="2.5" width="100"></td></tr><tr><td id="center">PhPMyAdmin</td><td><img src="https://zupimages.net/up/22/16/mfp5.png" alt="2" width="100"></td></tr><tr><td id="center">SQLite Studio</td><td><img src="https://zupimages.net/up/22/16/yj9j.png" alt="3" width="100"></td></tr></table></v-card-text></v-card><v-card elevation="0"><v-card-title><p>Plateformes / Frameworks</p></v-card-title><v-card-text><table><tr><th width="1%"> </th><th width="1%"> </th></tr><tr><td id="center">Flask</td><td> <img src="https://zupimages.net/up/22/16/9dl1.png" alt="2.5" width="100"></td></tr><tr><td id="center">Vue.JS</td><td><img src="https://zupimages.net/up/22/16/mfp5.png" alt="2" width="100"></td></tr><tr><td id="center">.NET</td><td><img src="https://zupimages.net/up/22/16/nubq.png" alt="1" width="100"></td></tr></table></v-card-text></v-card><v-card elevation="0"><v-card-title><p>Bases de données</p></v-card-title><v-card-text><table><tr><th width="1%"> </th><th width="1%"> </th></tr><tr><td id="center">PostgreSQL</td><td> <img src="https://zupimages.net/up/22/16/yj9j.png" alt="3" width="100"></td></tr><tr><td id="center">MySQL</td><td><img src="https://zupimages.net/up/22/16/yj9j.png" alt="3" width="100"></td></tr><tr><td id="center">SQLite</td><td><img src="https://zupimages.net/up/22/16/mfp5.png" alt="2" width="100"></td></tr></table></v-card-text></v-card><v-card elevation="0"